@import"https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Zen+Antique";.fruit .fruit__btn{padding:1rem;color:#fff;border-radius:15px;box-shadow:3px 3px 9px 3px rgba(0,0,0,.3);font-weight:bold;font-size:1rem}h1{font-size:48px}h2{font-size:36px}h3{font-size:24px}h4{font-size:18px}p{font-size:12px}.fruit{display:flex;flex-wrap:wrap;align-items:center;border:3px solid #000;border-radius:15px;max-width:28rem;margin:0 auto;justify-content:end;padding-right:2rem;margin:1rem auto;background-color:#433ac4;color:#fff;font-family:"Architects Daughter",cursive}@media screen and (max-width: 480px){.fruit{padding:.3rem;width:100%}}.fruit .fruit__checked{background-color:#3bbbbd;color:#fff}.fruit .fruit__checked:before{content:"✓";padding-right:1rem}.fruit .fruit__btn{background-color:#3bbbbd}.fruit .fruit__btn:hover{background:#000;transition-duration:1s !important;transition-timing-function:ease-in !important}.fruit h3{padding:1rem;margin:1rem;font-size:1.5rem;color:#06e2e2}@media(max-width: 500px){.fruit h3{padding:.3rem;font-size:1rem}}